.tabSlider {
  position: relative;
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;

  .section {

    &--title,
    &--desc {
      max-width: 828px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .slick-list {
    padding-right: 120px;
    padding-left: calc((100vw - 1110px) * .5);
    margin-left: calc((100vw - 1110px) * -.5);
    margin-right: calc((100vw - 1110px) * -.5);

    @media(max-width:992px) {
      padding-right: 0;
      margin-left: 0;
      margin-right: 0;
      padding-left: 0;
    }
  }

  .item {
    &--inner {
      margin-right: 68px;
      margin-left: 68px;

      @media (max-width: 575px) {
        margin-right: 12px;
        margin-left: 12px;
      }
    }
  }

  .slider-container {
    position: relative;
  }

  .slider-action {
    position: absolute;
    width: calc(100vw - 204px);
    justify-content: space-between;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media(max-width:575px) {
      width: calc(100vw - 30px);
    }
  }

  .slider-top-action {
    display: flex;
    align-items: center;
    max-width: fit-content;
    margin: 48px auto;

    @media(max-width: 575px) {
      // display: none;
    }

    a {
      display: flex;
      align-items: center;
      position: relative;
      font-size: 20px;
      line-height: 32px;
      font-weight: 600;
      color: rgba($color: $white, $alpha: 0.6);
      letter-spacing: 0.5px;
      padding: 0 28px 16px 56px;
      border-bottom: 2px solid rgba($color: $white, $alpha: 0.1);

      @media(max-width:992px) {
        flex-direction: column;
        padding-top: 56px;
        padding-left: 28px;
        text-align: center;
      }

      @media(max-width:767px) {
        font-size: 12px;
        line-height: 19px;
        padding-left: 14px;
        padding-right: 14px;
        padding-top: 28px;
      }

      &.active {
        color: rgba($color: $white, $alpha: 0.8);
        border-bottom: 2px solid rgba($color: $white, $alpha: 0.6);

        &.dashboard {
          &::before {
            background-image: get-icon('dashboard', rgba(255, 255, 255, 0.8));
          }
        }

        &.app {
          &::before {
            background-image: get-icon('app', rgba(255, 255, 255, 0.8));
          }
        }

        &.config {
          &::before {
            background-image: get-icon('config', rgba(255, 255, 255, 0.8));
          }
        }
      }

      &:before {
        position: absolute;
        content: '';
        min-width: 24px;
        height: 24px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        left: 24px;

        @media(max-width:992px) {
          top: 20px;
          left: 50%;
          transform: translateX(-50%);
        }

        @media(max-width:767px) {
          min-width: 18px;
          height: 18px;
          top: 0;
        }
      }

      &.dashboard {
        &::before {
          background-image: get-icon('dashboard', rgba(255, 255, 255, 0.6));
        }
      }

      &.app {
        &::before {
          background-image: get-icon('app', rgba(255, 255, 255, 0.6));
        }
      }

      &.config {
        &::before {
          background-image: get-icon('config', rgba(255, 255, 255, 0.6));
        }
      }
    }
  }
}