.cta {
  border-radius: 40px;
  border-top: 2px solid rgb($color: $white, $alpha: 0.1);
  border-left: 2px solid rgb($color: $white, $alpha: 0.1);
  border-right: 2px solid rgb($color: $white, $alpha: 0.1);
  background: rgba($color: $white, $alpha: 0.05);
  backdrop-filter: blur(12px);
  display: flex;
  align-items: center;
  padding: 12px 64px 12px 12px;

  @media(max-width:992px) {
    flex-wrap: wrap;
    padding-right: 22px;
    padding: 22px 22px 42px;
  }

  @media(max-width:575px) {
    padding: 12px 12px 32px;
    border-radius: 24px;
  }

  &__left {
    min-width: 40%;
    height: 540px;
    border-radius: 28px 0 0 28px;
    overflow: hidden;

    @media(max-width:992px) {
      min-width: 100%;
      border-radius: 28px 28px 0 0;
    }

    @media(max-width:575px) {
      height: 250px;
      border-radius: 12px 12px 0 0;
    }

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  &__right {
    padding-left: 64px;

    @media(max-width:992px) {
      padding-left: 0;
      padding-top: 32px;
    }
  }

  &__tag {
    color: $appKnite;
    font-size: 18px;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: 4px;
    text-transform: uppercase;
    margin-bottom: 12px;

    @media(max-width:575px) {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 8px;
    }
  }

  &__title {
    font-size: 48px;
    line-height: 68px;
    font-weight: 500;
    letter-spacing: 1px;
    margin-bottom: 48px;

    @media(min-width: 1920px) {
      font-size: 64px;
      line-height: 84px;
    }

    @media(max-width:992px) {
      font-size: 32px;
      line-height: 52px;
    }

    @media(max-width:575px) {
      font-size: 22px;
      line-height: 34px;
    }
  }
}