.tabSection {
  h2 {
    max-width: 828px;
    margin: 0 auto;
  }

  p {
    max-width: 828px;
    margin: 0 auto;
  }
}

// light-dark toggle switch
.lightDarkToggle {
  margin-top: 48px;

  .nav-tabs {
    margin: 0 auto;
    border-radius: 40px;
    max-width: 378px;
    border-bottom: none;
    padding: 8px;
    border-radius: 40px;
    border-top: 2px solid rgb($color: $white, $alpha: 0.1);
    border-left: 2px solid rgb($color: $white, $alpha: 0.1);
    border-right: 2px solid rgb($color: $white, $alpha: 0.1);
    // border: 2px solid rgba($color: $white, $alpha: 0.1);
    background: rgba($color: $white, $alpha: 0.05);
    backdrop-filter: blur(12px);
    box-sizing: content-box;
    justify-content: space-between;

    @media (max-width:575px) {
      max-width: 308px;
    }

    .nav-link {
      font-size: 20px;
      line-height: 32px;
      font-weight: 500;
      color: rgba($color: $white, $alpha: 0.6);
      outline: none;
      border: 2px solid transparent;
      box-shadow: none;
      letter-spacing: 0.5px;
      padding: 8px 20px 8px 16px;
      display: flex;
      align-items: center;
      white-space: nowrap;

      @media(max-width:575px) {
        font-size: 14px;
        line-height: 1.2;
      }

      &:hover {
        border: 2px solid transparent;
        outline: none;
      }

      &.active {
        border-top: 2px solid rgb($color: $white, $alpha: 0.1);
        border-left: 2px solid rgb($color: $white, $alpha: 0.1);
        border-right: 2px solid rgb($color: $white, $alpha: 0.1);
        border-bottom: transparent;
        background-color: rgba($color: $white, $alpha: 0.1);
        border-radius: 40px;
        color: rgba($color: $white, $alpha: 0.8);

        .toggle-icon {
          &.sun {
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-image: get-icon('sun', rgba(255, 255, 255, 0.8));
          }

          &.moon {
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-image: get-icon('moon', rgba(255, 255, 255, 0.8));
          }
        }
      }

      .toggle-icon {
        min-width: 24px;
        height: 24px;
        margin-right: 8px;

        @media(max-width: 575px) {
          min-width: 18px;
          height: 18px;
        }

        &.sun {
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          background-image: get-icon('sun', rgba(255, 255, 255, 0.6));
        }

        &.moon {
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          background-image: get-icon('moon', rgba(255, 255, 255, 0.6));
        }
      }
    }
  }

  .tab-content {
    margin-top: 48px;
  }
}