.process {
  &_slider {
    &-header {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      margin-bottom: 64px;

      @media(max-width:575px) {
        flex-wrap: wrap;
      }

      .left {
        max-width: 828px;

        @media(max-width:575px) {
          margin-bottom: 20px;
        }
      }
    }

    .slick-list {
      padding-right: 120px;
      padding-left: calc((100vw - 1111px) * .5);
      margin-left: calc((100vw - 1111px) * -.5);
      margin-right: calc((100vw - 1111px) * -.5);

      @media(max-width:992px) {
        padding-right: 0;
        margin-left: 0;
        margin-right: 0;
        padding-left: 0;
      }
    }
  }

  &__card {
    background: rgba($color: $white, $alpha: 0.05);
    padding: 40px;
    border-radius: 24px;
    width: 402px;
    border-top: 2px solid rgb($color: $white, $alpha: 0.1);
    border-left: 2px solid rgb($color: $white, $alpha: 0.1);
    border-right: 2px solid rgb($color: $white, $alpha: 0.1);
    // border: none;
    display: flex !important;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.2s ease;
    max-height: 512px;
    min-height: 512px !important;
    height: 512px;
    position: relative;
    cursor: pointer;
    margin-right: 24px;

    @media(max-width:992px) {
      max-height: 400px;
      min-height: 400px !important;
      height: 400px;
      padding: 28px;
    }

    @media(max-width:575px) {
      margin-right: 0;
      max-height: 350px;
      min-height: 350px !important;
      height: 350px;
      padding: 22px;
    }

    &:hover {
      background-color: rgba($color: $white, $alpha: 0.1);

      .top {
        display: none;
        opacity: 0;
      }

      .middle {
        padding-top: 0;
      }

      .process__card-desc {
        overflow: inherit;
        text-overflow: inherit;
        height: 160px;
        display: block;

        @media(max-width:575px) {
          height: inherit;
        }
      }
    }

    .process__card-desc {
      -webkit-line-clamp: 2;
      /* autoprefixer: off */
      -webkit-box-orient: vertical;
      /* autoprefixer: on */
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      height: 64px;
      transition: all 0.3s ease;

      @media(max-width:992px) {
        height: 50px;
      }

      @media(max-width:575px) {
        height: 48px;
      }
    }

    .top {
      transition: all 0.3s ease;

      p {
        font-size: 18px;
        line-height: 28px;
        letter-spacing: 4px;
        font-weight: 600;
        text-transform: uppercase;

        @media(max-width:992px) {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }

    .middle {
      padding: 96px 0;
      transition: all 0.3s ease;

      @media(max-width:992px) {
        padding: 40px 0;
      }
    }

    .tag {
      &__step {
        color: rgb($color: $white, $alpha: 0.4);
      }

      &__title {
        text-transform: uppercase;
        color: rgb($color: $white, $alpha: 0.6);
      }
    }
  }
}