@import "../../../node_modules/reset-css/reset";
@import "./typography";

html,
body {
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.5px;
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    background: #000;
    color: rgba($color: $white, $alpha: 0.6);
    scroll-behavior: smooth;
}

body {
    overflow-x: hidden;
    caret-color: $blue;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: none;
    }
}