.text {
  &--center {
    text-align: center;
  }

  &--primary {
    color: rgba($color: $white, $alpha: 0.8);
  }

  &--secondary {
    color: rgba($color: $white, $alpha: 0.6);

    &+p {
      margin-top: 15px;
    }
  }

  &--h1 {
    font-size: 44px;
    line-height: 58px;
    letter-spacing: 1px;

    @media(max-width: 992px) {
      font-size: 24px;
      line-height: 38px;
    }
  }

  &--h2 {
    font-size: 34px;
    line-height: 45px;
    letter-spacing: 1px;

    @media(max-width: 992px) {
      font-size: 18px;
      line-height: 38px;
    }
  }

  &--body {
    font-size: 20px;
    line-height: 32px;

    @media(max-width: 992px) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}