.col {
  &__container {
    &-lc {
      padding-left: 120px;

      @media(max-width:992px) {
        padding-left: 60px;
      }

      @media(max-width: 575px) {
        padding-left: 30px;
        padding-right: 30px;
      }
    }

    &-rc {
      padding-right: 120px;

      @media(max-width:992px) {
        padding-right: 60px;
      }

      @media(max-width: 575px) {
        padding-left: 30px;
        padding-right: 30px;
      }
    }

    .toggle__container {
      margin-left: 0;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }

  &__image {
    position: relative;
    height: 100%;
    // max-height: 710px;

    &-right {
      right: -30%;

      @media(max-width: 575px) {
        right: inherit;
        margin-top: 48px;
      }
    }

    &-left {
      left: -30%;

      @media(max-width: 575px) {
        left: inherit;
        margin-top: 48px;
      }
    }
  }
}