.gradient {
  &--appknit {
    background: linear-gradient(to left, rgba(0, 100, 224, 0.40) 0%, rgba(0, 100, 224, 0.00) 100%);
  }
}

.section {
  &--title {
    &+p {
      margin-top: 28px;

      @media(max-width: 575px) {
        margin-top: 16px;
      }
    }
  }

  &--padding {
    padding: 80px 0;

    @media(max-width: 575px) {
      padding: 32px 0;
    }
  }
}

.custom {
  &__container {
    padding-left: 120px;
    padding-right: 120px;

    @media(max-width: 992px) {
      padding-left: 60px;
      padding-right: 60px;
    }

    @media(max-width: 575px) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  &__btn {
    padding: 20px 28px;
    text-align: center;
    border-radius: 40px;
    border-radius: 40px;
    border-top: 2px solid rgb($color: $white, $alpha: 0.1);
    border-left: 2px solid rgb($color: $white, $alpha: 0.1);
    border-right: 2px solid rgb($color: $white, $alpha: 0.1);
    background: rgba($color: $white, $alpha: 0.05);
    backdrop-filter: blur(12px);
    font-size: 20px;
    line-height: 32px;
    font-weight: 600;
    letter-spacing: 0.5px;
    display: inline-block;
    color: rgba($color: $white, $alpha: 0.8);

    @media(max-width: 575px) {
      font-size: 16px;
      line-height: 24px;
      padding: 16px 24px;
    }

    &:hover {
      background: rgba($color: $white, $alpha: 0.10);
      color: rgba($color: $white, $alpha: 0.8);
    }

    &-arrow {
      display: inline-flex;
      align-items: center;
      position: relative;
      padding-right: 56px;

      @media(max-width:575px) {
        padding-right: 50px;
      }

      &:after {
        position: absolute;
        content: '';
        width: 24px;
        height: 24px;
        right: 24px;
        background: get-icon('arrow', rgba(255, 255, 255, 0.8));
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;

        @media(max-width: 575px) {
          width: 18px;
          height: 18px;
        }
      }
    }
  }
}

.slider-action {
  display: flex;
  align-items: center;

  a {
    min-width: 64px;
    height: 64px;
    border-radius: 50%;
    border-top: 2px solid rgb($color: $white, $alpha: 0.1);
    border-left: 2px solid rgb($color: $white, $alpha: 0.1);
    border-right: 2px solid rgb($color: $white, $alpha: 0.1);
    // border-bottom: 2px solid transparent;
    background: rgba($color: $white, $alpha: 0.05);
    backdrop-filter: blur(12px);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    transition: all 0.2s ease;

    @media(max-width:768px) {
      min-width: 38px;
      height: 38px;
    }

    &:hover {
      background: rgba($color: $white, $alpha: 0.10);
    }

    &:active {
      background: transparent;
      // border-bottom: 2px solid rgb($color: $white, $alpha: 0.1);

      &.btn-prev,
      &.btn-next {
        &:after {
          background-image: get-icon('arrow', rgba(255, 255, 255, 0.6));
        }
      }
    }

    &.slick-disabled {
      background: transparent;
      cursor: not-allowed;
      border-bottom: 2px solid rgb($color: $white, $alpha: 0.1);

      &.btn-prev,
      &.btn-next {
        &:after {
          background-image: get-icon('arrow', rgba(255, 255, 255, 0.4));
        }
      }
    }

    &+a {
      margin-left: 16px;
    }

    &.btn-prev {
      &:after {
        position: absolute;
        content: '';
        width: 24px;
        height: 24px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-image: get-icon('arrow', rgba(255, 255, 255, 0.8));
        transform: rotate(180deg);

        @media(max-width:768px) {
          width: 14px;
          height: 14px;
        }
      }
    }

    &.btn-next {
      &:after {
        position: absolute;
        content: '';
        width: 24px;
        height: 24px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-image: get-icon('arrow', rgba(255, 255, 255, 0.8));

        @media(max-width:768px) {
          width: 14px;
          height: 14px;
        }
      }
    }
  }
}

//toggle btn
.toggle {
  &__container {
    margin: 48px auto 0;
    border-radius: 40px;
    max-width: 378px;
    border-bottom: none;
    padding: 8px;
    border-radius: 40px;
    border-top: 2px solid rgb($color: $white, $alpha: 0.1);
    border-left: 2px solid rgb($color: $white, $alpha: 0.1);
    border-right: 2px solid rgb($color: $white, $alpha: 0.1);
    // border: 2px solid rgba($color: $white, $alpha: 0.1);
    background: rgba($color: $white, $alpha: 0.05);
    backdrop-filter: blur(12px);
    display: flex;
    align-items: center;
    box-sizing: content-box;
    justify-content: space-between;

    @media (max-width:575px) {
      max-width: 308px;
    }

    >a {
      font-size: 20px;
      line-height: 32px;
      font-weight: 500;
      color: rgba($color: $white, $alpha: 0.6);
      outline: none;
      border: 2px solid transparent;
      border-bottom: none;
      box-shadow: none;
      letter-spacing: 0.5px;
      padding: 8px 20px 8px 16px;
      display: flex;
      align-items: center;
      white-space: nowrap;

      @media(max-width:575px) {
        font-size: 14px;
        line-height: 1.2;
      }

      &:hover {
        border: 2px solid transparent;
        border-bottom: none;
        outline: none;
      }

      &.active {
        border-top: 2px solid rgb($color: $white, $alpha: 0.1);
        border-left: 2px solid rgb($color: $white, $alpha: 0.1);
        border-right: 2px solid rgb($color: $white, $alpha: 0.1);
        border-bottom: transparent;
        background-color: rgba($color: $white, $alpha: 0.1);
        border-radius: 40px;
        color: rgba($color: $white, $alpha: 0.8);

        .toggle-icon {
          &.sun {
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-image: get-icon('sun', rgba(255, 255, 255, 0.8));
          }

          &.moon {
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-image: get-icon('moon', rgba(255, 255, 255, 0.8));
          }
        }
      }

      .toggle-icon {
        min-width: 24px;
        height: 24px;
        margin-right: 8px;

        @media(max-width: 575px) {
          min-width: 18px;
          height: 18px;
        }

        &.sun {
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          background-image: get-icon('sun', rgba(255, 255, 255, 0.6));
        }

        &.moon {
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
          background-image: get-icon('moon', rgba(255, 255, 255, 0.6));
        }
      }
    }
  }
}

.mockup {
  border: 4px solid rgba($color: $white, $alpha: 0.1);
  border-radius: 24px;
  padding: 12px;
  display: flex;
  align-items: center;

  @media(min-width: 1600px) {
    padding: 32px;
    border-radius: 48px;
    border: 8px solid rgba($color: $white, $alpha: 0.1);
  }

  img {
    width: 100%;
  }

  &--light {
    background-color: rgba($color: $white, $alpha: 0.1);
  }

  &--dark {
    background-color: rgba($color: $black, $alpha: 0.3);
  }
}