.footer {
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__logo {
    display: inline-flex;
    align-items: center;
    padding: 4px 8px;
    margin-left: -8px;
    margin-right: -8px;
    border-radius: 12px;
    backdrop-filter: blur(12px);

    &:hover {
      background-color: rgba($color: $white, $alpha: 0.10);
    }

    a {
      font-size: 20px;
      line-height: 32px;
      letter-spacing: 0.5px;
      font-weight: 500;
      text-decoration: underline;
      color: rgba($color: $white, $alpha: 0.6);
      margin-left: 8px;
    }
  }

  &__title {
    max-width: 828px;
    margin: 16px 0 48px 0;
  }

  &__social {
    display: flex;
    align-items: center;

    .contact-btn {
      padding: 2px;
      border-radius: 40px;
      background: linear-gradient(#252a32, rgba($color: $white, $alpha: 0));
      color: rgba($color: $white, $alpha: 0.8);

      &:hover {
        >div {
          background-color: rgba(255, 255, 255, 0.02);
        }
      }

      >div {
        background-color: #0e121a;
        border-radius: 40px;
        padding: 20px 28px 20px 24px;
        display: flex;
        align-items: center;

      }
    }

    .icon {
      min-width: 24px;
      height: 24px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;

      &.contact {
        margin-right: 8px;
        background-image: get-icon('contact', rgba(255, 255, 255, 0.8));
      }
    }
  }
}